<template lang="pug">
    div(class="chartClass")
</template>

<script>

import vegaEmbed from "vega-embed";
export default {
    name: "BaseChart",
    props:{
        
        chartId:{
            type:String,
            default:"chart0",
        },
        chartSource:{
            type:Object,
            default:function(){
                return{}
            }
        },
        chartNum:{
            type: Number,
            default:0,
        }

    },

    methods:{
        chart(){
            console.log("",this.chartId);
            vegaEmbed(this.chartId,this.chartSource);
        }
    },



    watch:{
        // 
        chartNum(){
            this.chart();
        },
    }
}
</script>

<style>
.lineClass{
    width:100%
}
</style>